* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.service1-page {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f9f9f9;
  padding: 30px;
  overflow: hidden;
}

/* Removed logo-container styles to use direct positioning on the Logo itself */

.service1-page .Logo {
  height: 80px;
  width: 80px;
  position: absolute;
  top: 20px;
  right: 20px;
  transition: transform 0.3s ease;
  z-index: 10;
}

.service1-page .Logo:hover {
  transform: scale(1.05);
}

.back-button {
  position: absolute;
  bottom: 60px;
  left: 60px;
  width: 130px;
  height: 60px;
  background-color: transparent;
  border: 2px solid #1d3557;
  border-radius: 50px;
  color: #1d3557;
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
  font-family: "Tajawal", sans-serif;
  transition: all 0.3s ease;
  z-index: 10;
}

.back-button:hover {
  background-color: #1d3557;
  color: #ffffff;
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(29, 53, 87, 0.2);
}


.service1-page .text-wrapper-1 {
  color: #1d3557;
  direction: rtl;
  font-family: "Tajawal", sans-serif;
  font-size: clamp(18px, 3vw, 26px);
  font-weight: 400;
  position: absolute;
  top: 140px;
  text-align: right;
  z-index: 5;
}

.services-available-note {
  color: #bfbfbf;
  direction: rtl;
  font-family: "Tajawal", sans-serif;
  font-size: clamp(16px, 2.5vw, 18px);
  font-weight: 500;
  position: absolute;
  top: 180px;
  text-align: right;
  z-index: 5;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease, transform 0.5s ease;
}

.service1-icon {
  position: relative;
  width: 200px;
  height: 110px;
  margin-bottom: -10px;
  margin-top: -30px;
  filter: drop-shadow(0 8px 10px rgba(0, 0, 0, 0.1));
  animation: float 6s ease-in-out infinite;
}

.service2-icon {
  position: relative;
  width: 200px;
  height: 100px;
  margin-bottom: -10px;
  margin-top: -30px;
  filter: drop-shadow(0 8px 10px rgba(0, 0, 0, 0.1));
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0px);
  }
}

.BlueBox {
  position: absolute;
  top: -10px;
  left: -10px;
  width: 250px;
  opacity: 0.9;
  z-index: 1;
}

.RedBlueBox {
  position: absolute;
  bottom: -10px;
  right: -10px;
  width: 500px;
