body {
  font-family: "Tajawal", sans-serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: #f8f9fa;
}

.create-account-page {
  font-family: "Tajawal", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  overflow: hidden;
  position: relative;
  background: linear-gradient(135deg, #f5f5f5 0%, #e8eef4 100%);
}

.create-account-page .text-wrapper {
  color: #1d3557;
  direction: rtl;
  font-family: "Tajawal", Helvetica;
  font-weight: 700;
  font-size: 54px;
  letter-spacing: -0.5px;
  line-height: normal;
  top: 80px;
  width: 100%;
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.create-account-form {
  font-family: "Tajawal", sans-serif;
  width: 90%;
  max-width: 834px;
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  display: grid;
  grid-template-columns: 1fr 1fr;
  /* 2-column layout */
  grid-gap: 15px;
  gap: 15px;
  direction: rtl;
  box-sizing: border-box;
  margin-top: 50px;
  min-height: 400px;
  position: relative;
  height: auto !important;
  transition: none !important;
  z-index: 2;
}

.form-group {
  display: flex;
  flex-direction: column;
  position: relative;
  transition: transform 0.3s;
}

.form-group label {
  font-family: "Tajawal", sans-serif;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
}


.check-icon {
  animation: scaleIn 0.4s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}

.form-group input {
  font-family: "Tajawal", sans-serif;
  padding: 12px 15px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  font-size: 14px;
  color: #333;
  background-color: #f9f9f9;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.05);
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #234069;
  box-shadow: 0 0 0 3px rgba(35, 64, 105, 0.2);
  background-color: #fff;
  transition: all 0.2s ease-in-out;
}

.form-group input:hover:not(:focus):not(.readonly),
.form-group textarea:hover:not(:focus):not(.readonly) {
  border-color: #234069;
  transition: all 0.2s ease;
}

.form-group input::placeholder {
  color: rgba(0, 0, 0, 0.4);
}

.form-group input:focus::placeholder {
  opacity: 0.5;
}

.logo-container {
  position: absolute !important;
