/* Enhanced checkbox styling */
input[type="checkbox"] {
  position: relative;
  cursor: pointer;
  width: 22px;
  height: 22px;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: white;
  transition: all 0.3s ease;
}

input[type="checkbox"]:checked {
  background-color: #234069;
  border-color: #234069;
}

input[type="checkbox"]::after {
  content: '';
  position: absolute;
  top: 3px;
  left: 7px;
  width: 4px;
  height: 9px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  opacity: 0;
  transition: all 0.2s ease;
}

input[type="checkbox"]:checked::after {
  opacity: 1;
}

.terms.has-error input[type="checkbox"] {
  border-color: #e63a46;
  box-shadow: 0 0 0 1px rgba(230, 58, 70, 0.2);
}

.terms input[type="checkbox"]:checked {
  transform: scale(1.1);
}

.terms-link {
  color: #1d3557;
  text-decoration: none;
  margin: 0 5px;
  font-weight: 600;
  position: relative;
  transition: all 0.3s ease;
}

.terms-container {
  display: flex;
  align-items: center;
  grid-gap: 0.01rem;
  gap: 0.01rem;
  /* Adjust spacing between elements as needed */
}

.terms-link:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -2px;
  left: 0;
  background-color: #e63a46;
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.3s ease;
}

.terms-link:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.terms2 {
  font-family: "Tajawal", sans-serif;
