.modal {
  font-family: "Tajawal", sans-serif;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 100;
  animation: fadeIn 0.3s ease;
}

.modal-content {
  height: auto;
  min-height: 30%;
  width: 300px;
  background-color: white;
  padding: 25px;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  grid-gap: 15px;
  gap: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  animation: scaleIn 0.4s ease;
}

.modal-content-terms {
  height: auto;
  min-height: 50%;
  width: 700px;
  background-color: white !important;
  padding: 25px;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 15px;
  gap: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  animation: scaleIn 0.4s ease;
}

.modal h3 {
  margin: 0;
  color: #1d3557;
}

.modal input {
  padding: 12px;
  font-size: 16px;
  font-family: "Tajawal", sans-serif;
  width: 300px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 8px;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.modal input:focus {
  outline: none;
  border-color: #1d3557;
  box-shadow: 0 0 0 2px rgba(29, 53, 87, 0.2);
}

.modal button {
  font-family: "Tajawal", sans-serif;
  position: relative;
  top: 10px;
  padding: 12px 30px;
  background-color: #234069;
  color: white;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(35, 64, 105, 0.3);
}

.modal button:hover {
  background-color: #e63a46;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(230, 58, 70, 0.4);
}

.required {
  color: #e63a46;
  font-weight: bold;
  margin-right: 3px;
}

.terms input.required {
  margin-right: 5px;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes floatAnimation {

  0%,
  100% {
    transform: translateY(0) rotate(0deg);
  }

  50% {
    transform: translateY(-15px) rotate(2deg);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.8);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 4px 15px rgba(230, 58, 70, 0.4);
  }

  50% {
    box-shadow: 0 4px 25px rgba(230, 58, 70, 0.6);
  }

  100% {
    box-shadow: 0 4px 15px rgba(230, 58, 70, 0.4);
  }
}

/* Remove progress bar animation */
@keyframes progressAnimation {
  0% {
    width: 0%;
    opacity: 0;
  }

  100% {
    width: 0%;
    opacity: 0;
  }
}

/* Hide progress indicator for form completion */
.progress-bar {
  display: none;
}

/* Tooltip styles */
.tooltip {
  position: absolute;
  background-color: rgba(29, 53, 87, 0.9);
  color: white;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 12px;
  max-width: 200px;
  z-index: 100;
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.tooltip:after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: rgba(29, 53, 87, 0.9) transparent transparent transparent;
}

.form-group label:hover .tooltip {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

