/* CSS Animation classes instead of framer-motion */
.fade-in {
  animation: fadeIn 0.5s ease-in-out;
  /* Added 'forwards' to maintain final state */
}

.slide-up {
  animation: slideUp 0.5s ease-out;
}

.slide-down {
  animation: slideDown 0.5s ease-out;
}

.slide-in-left {
  animation: slideInLeft 0.8s ease-out;
}

.slide-in-right {
  animation: slideInRight 0.8s ease-out;
}

.scale-in {
  animation: scaleIn 0.4s ease-out;
}

.notification-animate {
  animation: slideDown 0.3s ease-out;
}

/* Apply animation-fill-mode to all animations */
.fade-in,
.slide-up,
.slide-down,
.slide-in-left,
.slide-in-right,
.scale-in,
.notification-animate {
  animation-fill-mode: forwards !important;
}

/* Animation keyframes */
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }

  to {
    opacity: 0.9;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(100px);
  }

  to {
    opacity: 0.9;
    transform: translateX(0);
  }
}

/* Create a subtle wave animation for the background */
.create-account-page::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(245, 245, 245, 0.3) 0%, rgba(232, 238, 244, 0.3) 100%);
  z-index: -1;
  animation: waveAnimation 15s infinite linear;
  background-size: 400% 400%;
}

@keyframes waveAnimation {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

