  margin: 8px 0;
  border-radius: var(--border-radius);
  color: var(--primary-color);
  font-weight: 500;
  font-size: 16px;
  cursor: pointer;
  transition: all var(--transition-speed);
  position: relative;
  grid-gap: 12px;
  gap: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.sidebar-item:hover,
.sidebar-item.active {
  background-color: var(--primary-color);
  color: white;
  box-shadow: 0 4px 8px rgba(29, 53, 87, 0.2);
}

.sidebar-icon {
  width: 22px;
  height: 22px;
  transition: filter var(--transition-speed);
}

.sidebar-item:hover .sidebar-icon,
.sidebar-item.active .sidebar-icon {
  filter: brightness(0) invert(1);
}

/* Main Content */
.main-content {
  flex: 1 1;
  padding: 30px;
  overflow-y: auto;
}

.section-title {
  font-size: 24px;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 30px;
  text-align: center;
}

/* Empty Dashboard */
.empty-dashboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.dashboard-placeholder {
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: var(--border-radius);
  padding: 40px;
  text-align: center;
  width: 100%;
  max-width: 600px;
  box-shadow: var(--box-shadow);
  margin-top: 40px;
}

.dashboard-placeholder p {
  font-size: 18px;
  color: var(--text-secondary);
}

/* Enhanced Project Section Styles */

/* Project Overview Card - Statistics */
.project-overview-card {
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin-bottom: 25px;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-overview-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
}

.project-overview-card .card-content {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.project-overview-card .stat-box {
  text-align: center;
  flex: 1 1;
  padding: 15px 10px;
  border-right: 1px solid var(--border-color);
}

.project-overview-card .stat-box:last-child {
  border-right: none;
}

.project-overview-card .stat-box h3 {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 10px;
}

.project-overview-card .stat-value {
  font-size: 24px;
  font-weight: 700;
  color: var(--primary-color);
}

/* Projects Filter Controls */
.projects-filter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  background-color: white;
  border-radius: var(--border-radius);
  padding: 15px 20px;
  box-shadow: var(--box-shadow);
}

.filter-group,
.sort-group {
  display: flex;
  align-items: center;
  grid-gap: 15px;
  gap: 15px;
}

.filter-group label,
.sort-group label {
  font-weight: 500;
  color: var(--text-primary);
}

.filter-options {
  display: flex;
  grid-gap: 8px;
  gap: 8px;
}

.filter-btn {
  background-color: #f5f5f5;
  border: none;
  border-radius: 20px;
  padding: 6px 15px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.filter-btn:hover {
  background-color: var(--primary-color);
  color: white;
}

.filter-btn.active {
  background-color: var(--primary-color) !important;
  color: white !important;
  ;
}

.sort-select {
  padding: 8px 15px;
  border-radius: 20px;
  border: 1px solid var(--border-color);
  background-color: white;
  font-size: 14px;
  cursor: pointer;
  min-width: 120px;
}

/* Enhanced Project Cards */
.projects-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 20px;
  gap: 20px;
  margin-bottom: 30px;
}

.project-card {
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  overflow: hidden;
  transition: transform var(--transition-speed), box-shadow var(--transition-speed);
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  border-color: rgba(0, 0, 0, 0.1);
}

.project-header {
  background-color: var(--primary-color);
  padding: 15px 20px;
  color: white;
}

.project-title-area {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project-title-area h3 {
  font-size: 18px;
  font-weight: 700;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex: 1 1;
}

.project-details {
  padding: 20px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  flex: 1 1;
}

.project-info-group {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 15px;
  gap: 15px;
}

.project-info-item {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  flex: 1 1;
  min-width: 120px;
}

.info-icon {
  color: var(--primary-color);
  font-size: 16px;
  width: 20px;
  text-align: center;
}

.info-content {
  display: flex;
  flex-direction: column;
}

.info-label {
  font-size: 13px;
  color: var(--text-secondary);
  margin-bottom: 2px;
}

.info-value {
  font-size: 15px;
  font-weight: 500;
  color: var(--text-primary);
}

.status-badge {
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 13px;
  font-weight: 600;
  display: inline-block;
  text-align: center;
}

/* Project Progress Bar */
.project-progress {
  margin: 5px 0;
}

.progress-label {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  font-size: 14px;
  color: var(--text-secondary);
}

.progress-percentage {
  font-weight: 600;
  color: var(--text-primary);
}

.progress-bar-container {
  height: 8px;
  background-color: #e0e0e0;
  border-radius: 4px;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  border-radius: 4px;
  transition: width 0.3s ease;
}

.progress-bar.مكتمل {
  background-color: var(--success-color);
}

.progress-bar.قيد-التنفيذ {
  background-color: var(--warning-color);
}

.progress-bar.متأخر {
  background-color: var(--error-color);
}

.progress-bar.اختبار-التشغيل {
  background-color: var(--info-color);
}

.project-actions {
  margin-top: auto;
  display: flex;
  justify-content: center;
}

.details-button {
  background-color: transparent;
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
  border-radius: 20px;
  padding: 8px 20px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-speed);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 8px;
  gap: 8px;
}

.button-icon {
  font-size: 14px;
}

.details-button:hover {
  background-color: var(--primary-color);
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(29, 53, 87, 0.2);
}

.details-button:active {
  transform: translateY(1px);
  box-shadow: none;
}

/* Empty Projects State */
.no-projects {
  background-color: white;
  border-radius: var(--border-radius);
  padding: 40px;
  text-align: center;
  box-shadow: var(--box-shadow);
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 15px;
  gap: 15px;
}

.empty-icon {
  font-size: 48px;
  color: #ccc;
  margin-bottom: 15px;
}

.create-project-btn {
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: var(--border-radius);
  padding: 10px 25px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-speed);
  margin-top: 20px;
}

.create-project-btn:hover {
  background-color: var(--primary-hover);
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(29, 53, 87, 0.2);
}

/* Enhanced Pagination */
.pagination {
  display: flex;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
  margin-top: 30px;
}

.page-button {
  background-color: white;
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 8px 14px;
  font-size: 14px;
  cursor: pointer;
  transition: all var(--transition-speed);
  font-family: "Tajawal", "DIN Next LT Arabic", sans-serif;
  min-width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-button:hover {
  background-color: #f0f0f0;
  border-color: #ccc;
}

.page-button.active {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
  font-weight: 600;
}

/* Responsive Adjustments */
@media (max-width: 1024px) {
  .projects-list {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }

  .project-overview-card .card-content {
    flex-wrap: wrap;
  }

  .project-overview-card .stat-box {
    flex: 1 1 45%;
    border-right: none;
    border-bottom: 1px solid var(--border-color);
    padding: 15px;
  }

  .project-overview-card .stat-box:nth-child(odd) {
    border-right: 1px solid var(--border-color);
  }

  .project-overview-card .stat-box:nth-last-child(-n+2) {
    border-bottom: none;
  }
}

@media (max-width: 768px) {
  .projects-list {
    grid-template-columns: 1fr;
  }

  .projects-filter {
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
    align-items: flex-start;
  }

  .filter-group,
  .sort-group {
    width: 100%;
  }

  .filter-options {
    flex-wrap: wrap;
  }

  .project-overview-card .stat-box {
    flex: 1 1 100%;
    border-right: none !important;
    border-bottom: 1px solid var(--border-color);
  }

  .project-overview-card .stat-box:last-child {
    border-bottom: none;
  }

  .pagination {
    flex-wrap: wrap;
    justify-content: center;
  }
}

