/* Loading Animation */
.loader {
  display: inline-block;
  position: relative;
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.loader:after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 2px solid white;
  border-color: white transparent white transparent;
  animation: loader 1.2s linear infinite;
}

@keyframes loader {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/* File Upload Styles */
.file-upload-container {
  position: relative;
  margin-bottom: 20px;
}

.file-upload-button {
  display: inline-block;
  padding: 12px 20px;
  background-color: var(--primary-color);
  color: white;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: all var(--transition-speed);
  text-align: center;
  font-weight: 500;
}

.file-upload-button:hover {
  background-color: var(--primary-hover);
}

.file-input {
  position: absolute;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  z-index: -1;
}

.file-name {
  margin-top: 10px;
  font-size: 14px;
  color: var(--text-secondary);
}

/* Tooltip */
.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltip-text {
  visibility: hidden;
  width: 200px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 10px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity 0.3s;
  font-size: 14px;
  font-weight: 400;
}

.tooltip:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

/* Print Styles */
@media print {
  .content-wrapper {
    display: block;
  }

  .main-content {
    padding: 0;
    width: 100%;
  }

  .company-profile,
  .profile-box {
    box-shadow: none;
    border: 1px solid #ccc;
  }

  body {
    background-color: white;
  }
}

/* Tabs */
.tabs-container {
  margin-bottom: 30px;
}

.tabs-list {
  display: flex;
  border-bottom: 1px solid var(--border-color);
  overflow-x: auto;
  padding-bottom: 1px;
}

.tab-item {
  padding: 12px 20px;
  font-size: 16px;
