/* Dropdown Menu */
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-toggle {
  background-color: white;
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 10px 15px;
  font-size: 14px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: 10px;
  gap: 10px;
  min-width: 150px;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  z-index: 100;
  min-width: 150px;
  max-height: 0;
  overflow: hidden;
  transition: max-height var(--transition-speed);
  visibility: hidden;
}

.dropdown-menu.show {
  max-height: 300px;
  visibility: visible;
}

.dropdown-item {
  padding: 10px 15px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color var(--transition-speed);
}

.dropdown-item:hover {
