}

.bm-result-page .Logo {
  height: 80px;
  width: 80px;
  position: absolute;
  top: 20px;
  right: 20px;
  transition: transform 0.3s ease;
  z-index: 10;
}

.bm-result-page .Logo:hover {
  transform: scale(1.05);
}

/* Navigation Style */
.bm-result-page .LeftArrow {
  transition: transform var(--transition-speed);
