}

/* Page Title with enhanced typography */
.bm-result-page h1 {
  font-size: 2.5rem;
  color: var(--text-color);
  margin-top: 90px;
  margin-bottom: 30px;
  font-weight: 700;
  text-shadow: 1px 1px 2px var(--shadow-light);
  position: relative;
  z-index: 1;
}

.bm-result-page h1::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
