  justify-content: center;
}

.loading::after {
  content: "...";
  animation: loadingDots 1.5s infinite;
  width: 24px;
  display: inline-block;
  text-align: left;
}

@keyframes loadingDots {

  0%,
  20% {
    content: ".";
  }

  40% {
    content: "..";
  }

  60%,
  100% {
    content: "...";
  }
}

/* Error Message Styling */
.error-msg {
  background-color: #fff0f0;
