}

.bmc-canvas[dir="rtl"] .bmc-top,
.bmc-canvas[dir="rtl"] .bmc-bottom {
  flex-direction: row-reverse !important;
}

/* Clear any conflicting RTL/LTR rules */
[dir="rtl"] .bmc-top,
[dir="rtl"] .bmc-bottom {
  /* Reset any global RTL rules that might be affecting these elements */
  direction: inherit;
  text-align: inherit;
}
