/* Enhanced timeline styling */
.timeline {
  position: relative;
  /* Establishes a containing block */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}

/* Create a continuous line behind the circles */
.timeline::before {
  content: "";
  position: absolute;
  top: 50%;
  /* Vertical center of the container */
  left: 0;
  height: 3px;
  background-color: #e0e0e0;
  z-index: 1;
  /* Behind the circles */
  transform: translateY(-50%);
}

/* The circle styling */
.circle {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #e0e0e0;
  position: relative;
  transition: transform 0.4s ease, background-color 0.4s ease;
  z-index: 2;
  /* Ensures the circles appear on top of the line */
}

/* Active circle styling */
.circle.active {
  background-color: var(--primary);
  transform: scale(1.2);
  box-shadow: 0 0 0 4px rgba(230, 58, 70, 0.2);
}

.line {
