}

.timeline-task-item:last-child {
  margin-bottom: 0;
}

.task-content {
  display: flex;
  align-items: flex-start;
  grid-gap: 0.75rem;
  gap: 0.75rem;
  flex: 1 1;
}

.task-icon {
  color: var(--primary-color);
  font-size: 0.9rem;
  background-color: rgba(var(--primary-color-rgb), 0.1);
