}

.milestones-table tr:last-child td {
  border-bottom: none;
}

/* Task List Styling */
.task-list {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.task-item {
  display: flex;
  align-items: flex-start;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  padding: 0.35rem 0;
}

.task-bullet {
  display: inline-block;
  width: 8px;
  height: 8px;
  background-color: var(--primary-color);
  border-radius: 50%;
  margin-top: 0.5rem;
}

.task-text {
  flex: 1 1;
  font-size: 0.95rem;
  line-height: 1.5;
}

/* Risk Factors Styling */
.risk-factors {
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.risk-factor-item {
  display: flex;
  align-items: flex-start;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  padding: 0.35rem 0;
}

.risk-icon {
  color: var(--warning-color);
  font-size: 0.9rem;
  margin-top: 0.2rem;
}

/* Time Estimate Styling */
.time-estimate {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.time-number {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--primary-color);
}

.time-unit {
  font-size: 0.9rem;
  color: var(--text-muted);
}

/* Action Buttons */
.action-buttons {
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin-top: 0.75rem;
  justify-content: center;
}

.action-btn {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
