  margin-top: 0.2rem;
}

.timeline-progress {
  margin: 1rem 0;
}

.timeline-progress .progress-container {
  height: 8px;
  margin-bottom: 0.5rem;
}

.progress-percentage {
  font-size: 0.85rem;
  color: var(--text-muted);
  display: block;
  text-align: left;
}

.timeline-meta {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid var(--border-color);
}

.meta-item {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  font-size: 0.85rem;
  color: var(--text-muted);
  background-color: var(--light-bg);
  padding: 0.5rem 0.75rem;
  border-radius: 1rem;
}

.meta-item.risk {
  font-weight: 600;
}

.meta-item.risk.risk-high {
  background-color: rgba(220, 53, 69, 0.1);
  color: var(--danger-color);
}

.meta-item.risk.risk-medium {
  background-color: rgba(255, 193, 7, 0.1);
  color: var(--warning-color);
}

.meta-item.risk.risk-low {
  background-color: rgba(40, 167, 69, 0.1);
  color: var(--success-color);
}

/* Risks View */
.risks-view {
  display: block;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
  padding: 1rem 0;
}

.risks-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  justify-content: flex-end;
