  border-bottom: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.02);
}

.risk-header h3 {
  font-weight: 700;
  font-size: 1.1rem;
  margin: 0;
  color: var(--primary-color);
}

.risk-level {
  padding: 0.35rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 600;
}

.risk-level.risk-high {
  background-color: var(--danger-color);
  color: white;
}

.risk-level.risk-medium {
  background-color: var(--warning-color);
  color: white;
}

.risk-level.risk-low {
  background-color: var(--success-color);
  color: white;
}

.risk-body {
  padding: 1.25rem;
}

.risk-factors-list h4 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  color: var(--text-color);
}

.risk-factor {
  display: flex;
  align-items: flex-start;
  grid-gap: 0.75rem;
  gap: 0.75rem;
  padding: 0.5rem 0;
  border-bottom: 1px dashed var(--border-color);
}

.risk-factor:last-child {
  border-bottom: none;
}

.risk-factor i {
  color: var(--danger-color);
  margin-top: 0.25rem;
}

@media (max-width: 992px) {
  .risks-view {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .risks-view {
    grid-template-columns: 1fr;
  }
}

/* Loading and Error States */
.loading-spinner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5rem 3rem;
  min-height: 300px;
}

.spinner {
  width: 60px;
  height: 60px;
  border: 5px solid rgba(var(--primary-color-rgb), 0.1);
  border-radius: 50%;
  border-left-color: var(--primary-color);
  animation: spin 1.2s linear infinite;
  margin-bottom: 2rem;
  box-shadow: 0 0 20px rgba(var(--primary-color-rgb), 0.1);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loading-spinner p {
  font-size: 1.2rem;
  color: var(--text-muted);
  text-align: center;
  font-weight: 500;
}

.error-message {
  background-color: rgba(220, 53, 69, 0.1);
  color: var(--danger-color);
  padding: 2rem;
  border-radius: var(--border-radius);
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  text-align: center;
  box-shadow: 0 5px 15px rgba(var(--danger-color-rgb), 0.1);
  border: 1px solid rgba(var(--danger-color-rgb), 0.2);
}

.error-message i {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.error-message p {
  font-size: 1.1rem;
  line-height: 1.6;
  max-width: 500px;
  margin: 0;
}

.no-data-message {
  text-align: center;
  padding: 3rem;
  color: var(--text-muted);
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.no-data-message::before {
  content: '\f119';
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  font-size: 3rem;
  color: var(--text-muted);
  opacity: 0.5;
}

